import React, { useEffect, useState } from "react";
import { PlayCircleFilled, BackwardFilled } from "@ant-design/icons";
import player from "../../video/123.mp3";
import { Image } from "antd";
//import dayjs from "dayjs";
const HomePage = () => {
    const [userInput, setUserInput] = useState("");
    const dateFormat = "YYYY/MM/DD";
    const MM = ["正月", "二月", "三月", "四月", "五月", "六月", "七月", "八月", "九月", "十月", "冬月", "腊月"];
    const DD = [
        "初一",
        "初二",
        "初三",
        "初四",
        "初五",
        "初六",
        "初七",
        "初八",
        "初九",
        "初十",
        "十一",
        "十二",
        "十三",
        "十四",
        "十五",
        "十六",
        "十七",
        "十八",
        "十九",
        "廿一",
        "廿二",
        "廿三",
        "廿四",
        "廿五",
        "廿六",
        "廿七",
        "廿八",
        "廿九",
        "三十",
        "三十一",
    ];
    const geshihua = (number) => {
        return number > 9 ? "" + number : "0" + number;
    };
    const data = () => {
        let newdata = new Date();
        let olddata = new Date(new Date().getTime() - 24 * 86400000);
        let daojishi =
            new Date("2022/12/28").getTime() - new Date("2022/12/29").getTime() > 0
                ? ` ${geshihua(23 - olddata.getHours())}：${geshihua(60 - olddata.getMinutes())}：${geshihua(60 - olddata.getSeconds())}`
                : "fnished";
        return [
            `${newdata.getFullYear()}年${geshihua(newdata.getMonth() + 1)}月${geshihua(newdata.getDate())}日 ${geshihua(
                newdata.getHours()
            )}：${geshihua(newdata.getMinutes())}：${geshihua(newdata.getSeconds())}`,
            `${olddata.getFullYear()}年${MM[olddata.getMonth()]}${DD[olddata.getDate()]}`,
            daojishi,
        ];
    };
    useEffect(() => {
        const boxes = document.querySelectorAll(".box");
        //wheel 该事件在鼠标滚轮在元素上下滚动时触发
        //scroll 当文档被滚动时发生的事件。
        document.getElementById("main").addEventListener("scroll", function () {
            console.log(boxes);
            const triggerBottom = (window.innerHeight / 5) * 4 - 100;
            boxes.forEach((box) => {
                //getBoundingClientRect元素距离页面顶部的距离
                const boxTop = box.getBoundingClientRect().top;
                const boxBottom = box.getBoundingClientRect().bottom;
                console.log(boxTop);
                if (boxBottom < 150 || boxTop < 150) {
                    box.classList.add("opacity");
                } else {
                    box.classList.remove("opacity");
                }

                if (boxTop < triggerBottom) {
                    box.classList.add("show");
                } else {
                    box.classList.remove("show");
                }
            });
        });
    }, []);
    const [dataArray, setdataArray] = useState([" ", " ", ""]);
    const [bofang, setbofang] = useState(true);
    const [happy, sethappy] = useState(true);

    useEffect(() => {
        //setdataArray(data());

        setInterval(() => {
            setdataArray(data());
        }, 1000);
    }, []);
    useEffect(() => {
        bofang ? document.getElementById("video1").play() : document.getElementById("video1").pause();
    }, [bofang]);
    return (
        <div className="main" id="main">
            <div className="center" style={{ marginTop: "16px" }}>
                <div style={{ position: "fixed", top: 0, right: 0, width: "100%", textAlign: "center", zIndex: 2, marginTop: "16px" }}>
                    <h1>今天是{dataArray[0]}</h1>
                    <h2>农历{dataArray[1]}</h2>
                    <h2>{dataArray[2] === "fnished" ? "祝小王同学生日快乐！！" : "某个神秘的倒计时：" + dataArray[2]}</h2>
                    {happy ? (
                        <div>
                            <audio id="video1" loop="loop">
                                <source src={player} type="audio/mp3" />
                            </audio>
                            {bofang ? (
                                <PlayCircleFilled
                                    onClick={() => {
                                        setbofang((i) => !i);
                                    }}
                                    style={{ fontSize: "30px" }}
                                />
                            ) : (
                                <BackwardFilled
                                    style={{ fontSize: "30px" }}
                                    onClick={() => {
                                        setbofang((i) => !i);
                                    }}
                                />
                            )}
                        </div>
                    ) : null}
                </div>

                {happy ? (
                    <div className="body" id="body">
                        <div id="box" style={{ marginTop: "70px", height: "400px" }} className="box show">
                            <h3 style={{ margin: "10px 0" }}>遥叩芳辰，生辰吉乐</h3>
                            <p style={{ margin: "10px 0" }}>虽然不在身边，但祝福一定要送到！！</p>
                            <p>(虽然有点土，但心是真的，嘿嘿嘿)</p>
                            <Image src="https://img.xiangmu.com/uploadpic/201710/07/0715370591395754.gif" />
                        </div>
                        <div id="box" className="box show">
                            <h3>一岁一礼，一寸欢喜</h3>
                            <h4>愿意和你度过每一年的生日</h4>
                            <h4>阿樂希望你永远开心快乐！！</h4>
                            <h4>以后的日子你负责开心，我负责逗你开心</h4>
                        </div>
                        <div id="box" className="box">
                            <h3>旦逢良辰，顺颂时宜</h3>
                            <h4>希望以后的我们顺顺利利，万事皆宜</h4>
                            <h4>和你度过的每一时刻都是我最难忘的回忆</h4>
                            <h4>成长是上天赐予的最好礼物，</h4>
                            <h4>以后的日子，愿与君共勉</h4>
                        </div>
                        <div id="box" className="box">
                            <h3>岁岁春无事，相逢总玉颜</h3>
                            <h4>小仙女当然永远是十八岁啦</h4>
                            <h4>
                                希望我的小仙女年年岁岁平安喜<span style={{ fontSize: "25px" }}>乐</span>
                            </h4>
                        </div>
                        <div id="box" className="box">
                            <h3> 从今把定春风笑，且做人间长寿仙</h3>
                            <h4>心情舒畅，潇潇洒洒是你以后的主旋律</h4>
                            <h4>非常喜欢你的乐观（主要是心大，哈哈哈）</h4>
                            <h4>所有的愁眉苦脸都将成为过去式！！！</h4>
                        </div>
                        <div id="box" className="box">
                            <h3>鸳鸯交颈期千岁，琴瑟和谐愿百年</h3>
                            <h4>我们终将成为别人羡慕的样子，我说的！！（自信满满）</h4>
                            <h4>祝你永远平安喜乐！喜乐！喜乐！</h4>
                            <h4>（重要的事情说三遍）</h4>
                        </div>
                        <div id="box" className="box">
                            <h3>请记住，你可以永远相信阿樂</h3>
                        </div>
                        <div id="box" className="box">
                            <h3>你的阿樂献上！！！</h3>
                        </div>
                    </div>
                ) : (
                    <div className="body" id="body">
                        <div id="box" className="box show">
                            <h3>今年过的开心吗？</h3>
                            <h4>答应我，马上又要长大一岁了，所有的烦恼统统忘掉！！</h4>
                        </div>
                        <div id="box" className="box">
                            <h4>后续内容将在生日准时解锁，记得回来看哦....</h4>
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
};

export default HomePage;
